.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.App h1 {
  text-align: center;
  width: 100%;
}

button {
  display: flex;
  margin: 0 auto;
  font-size: 1.2rem;
}

.buttons-title {
  margin-bottom: 0;
}

.options {
  margin-left: 75px;
}

aside {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 25%;
  min-width: 300px;
  height: 80vh;
  border: 2px solid black;
}

aside h3 {
  margin-bottom: 0;
}

.default-img {
  max-width: 70%;
  margin-top: 10px;
}

.buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

.option-button {
  display: none;
  margin: 5px;
}

/*
 * Media Queries
 */
@media screen and (max-width: 608px) {
  aside {
    margin: 20px 0;
  }
}

@media screen and (min-width: 800px) {
  aside {
    height: 75vh;
  }
}